import { Box, Button, Typography, TextField, MenuItem, InputAdornment, IconButton, Popover, CircularProgress } from "@mui/material";
import { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { testReadyStyles } from "src/components/styles/customStyles";
import Images from "src/assets";
import { Close } from "@mui/icons-material";
import AssignmentLateIcon from '@mui/icons-material/AssignmentLate';
import { gradeScaleOptions } from "src/assets/constants/misc";

const AcademicScore = () => {
  const navigate = useNavigate();
  const location = useLocation();

  // Get data passed via navigation state
  const { title: pageTitle } = location.state || {};

  // Debugging: Check if the title is being received correctly
  //console.log("Received Title:", pageTitle);

  // Set the title dynamically based on what was received
  const [title, setTitle] = useState(pageTitle || "Enter your academic details");
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [institutions, setInstitutions] = useState<{ InstitutionName: string }[]>([]);
  const [loading, setLoading] = useState(false);
  const [showDropdown,setShowDropdown]=useState(false);

  useEffect(() => {
    if (pageTitle) {
      setTitle(pageTitle);
    }
  }, [pageTitle]); 

  const [formData, setFormData] = useState({
    institution: "",
    academicLevel: "",
    gradeSystem: "",
    score: "",
    backlogs: "",
    maxScore: null as number | null 
  });

  const [errors, setErrors] = useState({
    institution: "",
    academicLevel: "",
    gradeSystem: "",
    score: "",
    backlogs: ""
  });


  useEffect(() => {
    if (formData.institution.length < 2) {
      setInstitutions([]);
      setShowDropdown(false);
      return;
    }
  }, [formData.institution]);
  


  const localStorageHandler=(academicLevel:string )=>{
    console.log("Selected Level",academicLevel);
  if (formData.gradeSystem === "percentage") formData.maxScore = 100;
    if (formData.gradeSystem === "cgpa1") formData.maxScore = 10;
  if (formData.gradeSystem === "cgpa2") formData.maxScore = 7;
  if (formData.gradeSystem === "cgpa3") formData.maxScore = 5;
  if (formData.gradeSystem === "gpa") formData.maxScore = 4;

    if(academicLevel==="masters")
  {
    setPostgraduation({institution:formData.institution,backlogs:formData.backlogs,gradingSystem:formData.gradeSystem,totalScore:formData.score});
  }
  if(academicLevel==="grade 12")
  {
    setIntermediate({institution:formData.institution,backlogs:formData.backlogs,gradingSystem:formData.gradeSystem,totalScore:formData.score});
  }
  if(academicLevel==="bachelors")
  {
    setUndergraduation({institution:formData.institution,backlogs:formData.backlogs,gradingSystem:formData.gradeSystem,totalScore:formData.score, maxScore: formData.gradeSystem === "cgpa1" ? 10 : formData.gradeSystem === "cgpa2" ? 7 : formData.gradeSystem==='cgpa3'? 5:formData.gradeSystem==='gpa'?4:formData.gradeSystem==='percentage'?100:undefined});
  }
  if(academicLevel==="others")
  {
    setOthers({institution:formData.institution,backlogs:formData.backlogs,gradingSystem:formData.gradeSystem,totalScore:formData.score});
  }
  }

  const setPostgraduation=(collectedData:{institution: string,backlogs:string,gradingSystem:string,totalScore:number|string})=>{
    console.log("Masters Data",collectedData);
    let currentValue=localStorage.getItem("user-profile");
    let currentValueObj=currentValue?JSON.parse(currentValue):{}
    if(currentValueObj)
    {
      localStorage.setItem("user-profile",JSON.stringify({...currentValueObj,education:{ postGraduation:{instituteName:collectedData.institution,
        backlogs:collectedData.backlogs,totalScore:collectedData.totalScore,gradingSystem:collectedData.gradingSystem
      }}}));	
    }
  }

  const setUndergraduation=(collectedData:{institution: string,backlogs:string,gradingSystem:string,totalScore:number|string,  maxScore?: number;})=>{
    let currentValue=localStorage.getItem("user-profile");
    let currentValueObj=currentValue?JSON.parse(currentValue):{};
    if(currentValueObj)
    {
      localStorage.setItem("user-profile",JSON.stringify({...currentValueObj,education:{ underGraduation:{instituteName:collectedData.institution,
        backlogs:collectedData.backlogs,totalScore:collectedData.totalScore,gradingSystem:collectedData.gradingSystem,...(collectedData.maxScore && { maxScore: collectedData.maxScore }), 
      }}}));	
    }
  }
  const setIntermediate=(collectedData:{institution: string,backlogs:string,gradingSystem:string,totalScore:number|string})=>{
    let currentValue=localStorage.getItem("user-profile");
    let currentValueObj=currentValue?JSON.parse(currentValue):{};
    if(currentValueObj)
    {
      localStorage.setItem("user-profile",JSON.stringify({...currentValueObj,education:{ plus2:{instituteName:collectedData.institution,
        backlogs:collectedData.backlogs,totalScore:collectedData.totalScore,gradingSystem:collectedData.gradingSystem
      }}}));	
    }
  }
  const setOthers=(collectedData:{institution: string,backlogs:string,gradingSystem:string,totalScore:number|string})=>{
    let currentValue=localStorage.getItem("user-profile");
    let currentValueObj=currentValue?JSON.parse(currentValue):{};
    if(currentValueObj)
    {
      localStorage.setItem("user-profile",JSON.stringify({...currentValueObj,education:{ plus2:{instituteName:collectedData.institution,
        backlogs:collectedData.backlogs,totalScore:collectedData.totalScore,gradingSystem:collectedData.gradingSystem
      }}}));	
    }
  }
  
 
  
  // Handle input changes
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
      ...(name === "gradeSystem"
        ? {
            maxScore: value === "cgpa1" ? 10 : value === "cgpa2" ? 7 : value==='cgpa3'?5:value==='gpa'?4:value==="percentage"?100:null, // Set maxScore based on gradeSystem
            score: "",
            backlogs: ""
          }
        : {})
    }));

    // Validate fields dynamically
    validateField(name, value);
  };



  // Validation logic
  const validateField = (name: string, value: string) => {
    let error = "";

    switch (name) {
      case "institution":
        error = value.trim() === "" ? "Institution name is required" : "";
        break;
      case "academicLevel":
        error = value === "" ? "Please select an academic level" : "";
        break;
      case "gradeSystem":
        error = value === "" ? "Please select a grade system" : "";
        break;
      case "score":
        if (!formData.gradeSystem) {
          error = "Select a grade system first";
        } else if (formData.gradeSystem === "percentage") {
          // Allow numbers with up to 2 decimal places, between 0-100
          const percentageRegex = /^(100(\.00?)?|([0-9]?[0-9])(\.\d{1,2})?)$/;
          if (!percentageRegex.test(value)) {
            error = "Enter a valid percentage (0-100) with up to 2 decimals";
          }
        }
        else if(formData.gradeSystem === "cgpa1"){
          const cgpa1Regex = /^10(\.00?)?$|^([0-9](\.\d{1,2})?)$/;
          if (!cgpa1Regex.test(value)) {
            error = "Enter a valid CGPA (0-10) with up to 2 decimals";
          }
        }
          else if (formData.gradeSystem === "cgpa2") {
            // Validation for CGPA out of 7 (0-7, up to 2 decimals)
            const cgpa2Regex = /^7(\.00?)?$|^([0-6](\.\d{1,2})?)$/;
            if (!cgpa2Regex.test(value)) {
              error = "Enter a valid CGPA (0-7) with up to 2 decimals";
            }
        }else if (formData.gradeSystem === "cgpa3") {
          // Validation for CGPA out of 7 (0-7, up to 2 decimals)
          const cgpa3Regex = /^7(\.00?)?$|^([0-5](\.\d{1,2})?)$/;
          if (!cgpa3Regex.test(value)) {
            error = "Enter a valid CGPA (0-7) with up to 2 decimals";
          }
      }
          else if (formData.gradeSystem === "gpa") {
      // Validation for CGPA out of 7 (0-7, up to 2 decimals)
       const gpa = /^7(\.00?)?$|^([0-4](\.\d{1,2})?)$/;
      if (!gpa.test(value)) {
        error = "Enter a valid GPA (0-4) with up to 2 decimals";
       }
  }
  else if (formData.gradeSystem === "grade") {
    // Validation for letter grades (A-Z, a-z)
    const gradeRegex = /^[A-Za-z]$/;
    if (!gradeRegex.test(value)) {
      error = "Enter a valid grade (A-Z)";
    }
  }
        
        else if (value === "" || parseFloat(value) <= 0) {
          error = "Enter a valid score";
        }
        break;
      case "backlogs":
        error = value !== "" && parseInt(value) < 0 ? "Backlogs cannot be negative" : "";
        break;
      default:
        break;
    }

    setErrors((prevErrors) => ({ ...prevErrors, [name]: error }));
  };

  // Check if all fields are valid before enabling Save button
  const isFormValid = Object.values(errors).every((err) => err === "") &&
                      Object.values(formData).every((val) => val !== "");

  

  const handleSave = () => {
    localStorage.setItem("formData", JSON.stringify(formData));
    navigate("/testcondition1");
    localStorageHandler(formData.academicLevel)
  };
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    const savedData = localStorage.getItem("formData");
    if (savedData) {
      setFormData(JSON.parse(savedData)); // Set the state with the saved data
    }
  }, []); 

  const handleOptionClick = (option: { InstitutionName: string }) => {
    setFormData((prev) => ({ ...prev, institution: option.InstitutionName }));
    setInstitutions([]); // Hide suggestions after selection
    setShowDropdown(false)
  };

  const handleInstitueSearch=(e: React.ChangeEvent<HTMLInputElement>)=>{
    console.log("Change")
    const fetchData = async (search:string) => {
      setLoading(true);
      try {
        const response = await fetch(
          `https://campusroot.com/api/v1/public/university/?search=${search}&institutions=1&universities=0&disciplines=0&subDisciplines=0&location=0&perPage=100&page=1&custom=true`
        );
        const data = await response.json();
        setInstitutions(data?.data?.institutions
          || []);
        setShowDropdown(true);
      } catch (error) {
        console.error("Error fetching institutions:", error);
      }
      setLoading(false);
    };
    fetchData(e.target.value);
    handleChange(e)
  }

  const open = Boolean(anchorEl);
  const id = open ? "info-popover" : undefined;

  console.log("Data",showDropdown);

  return (
    <Box
      sx={{
        ...testReadyStyles.container1,
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        textAlign: "center",
      }}
    >
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 3, fontSize: { xs: "1.5rem", lg: "2rem", },color:"#495052" }}>
        {title.includes("final score") ? (
        <>
          What is your <span style={{ color: "#F48438"}}>final score</span> status?
        </>
      ) : (
        <>
          Enter your <span style={{ color: "#F48438" }}>current academic</span> details
        </>
      )}
      </Typography>

      {/* Input Fields */}
      <Box sx={{ width: { xs: "90%", sm: "400px" }, display: "flex", flexDirection: "column", gap: 2 }}>
        <div style={{position:"relative"}}>
          <TextField
          fullWidth
          name="institution"
          label="Enter your school, college, or university name"
          variant="outlined"
          value={formData.institution}
          onChange={handleInstitueSearch}
          error={!!errors.institution}
          helperText={errors.institution}
          autoComplete="off"
          InputProps={{
            endAdornment: (
              <>
                {loading && <CircularProgress color="inherit" size={20} />}
                {formData.institution && (
                  <Close
                    sx={{ cursor: "pointer" }}
                    onClick={() => setFormData((prev) => ({ ...prev, institution: "" }))}
                  />
                )}
              </>
            ),
          }}
          sx={{
            position:"relative",
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
              "& fieldset": { borderLeft: "10px solid #FF9784" },
            },
          }}
        />
        {showDropdown && (
          <Box
            sx={{
              position: "absolute",
              top:'100%',
              zIndex: 100,
              backgroundColor: "#fff",
              boxShadow: 1,
              maxHeight: "200px",
              overflowY: "auto",
              width: "100%",
            }}
          >
            {institutions.map((option, index) => (
              <MenuItem  key={index}
              onClick={() => handleOptionClick(option)}>
                {option?.InstitutionName
                }
              </MenuItem>
            
              
            ))}
          </Box>
        )}
        </div>
        <TextField
          fullWidth
          select
          name="academicLevel"
          label={title.includes("final score")?"Select your last academic":'select your current academic'}
          variant="outlined"
          value={formData.academicLevel}
          onChange={handleChange}
          error={!!errors.academicLevel}
          helperText={errors.academicLevel}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
              "& fieldset": { borderLeft: "10px solid #AAAEFE" },
            },
          }}
        >
          <MenuItem value="masters" sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>Masters</MenuItem>
          <MenuItem value="bachelors" sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>Bachelors</MenuItem>
          <MenuItem value="diploma" sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>Diploma</MenuItem>
          <MenuItem value="grade 12" sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>Grade 12</MenuItem>
          <MenuItem value="others" sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>Others</MenuItem>
        </TextField>

        <TextField
          fullWidth
          select
          name="gradeSystem"
          label="Grade system"
          variant="outlined"
          value={formData.gradeSystem}
          onChange={handleChange}
          error={!!errors.gradeSystem}
          helperText={errors.gradeSystem}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
              "& fieldset": { borderLeft: "10px solid #70A6B2" },
            },
          }}
        >
          {
            gradeScaleOptions.map((item) =>
              <MenuItem value={item.value} sx={{ justifyContent: 'flex-start', textAlign: 'left' }}>{item.label}</MenuItem>
            )
          }
        </TextField>

        <TextField
          fullWidth
          name="score"
          label="Enter Score"
          variant="outlined"
          value={formData.score}
          onChange={handleChange}
          error={!!errors.score}
          helperText={errors.score}
          type={formData.gradeSystem === "grade" ? "text" : "number"} // Change input type dynamically
             inputProps={{
                        ...(formData.gradeSystem === "grade"
                    ? { maxLength: 1, pattern: "[A-Za-z]" } // Allow only one letter for grades
                    : { step: "0.01" }) // Allow decimal numbers for other systems
                       }}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
              "& fieldset": { borderLeft: "10px solid #AAAEFE" },
            },
          }}
        />

<Popover
  id={id}
  open={open}
  anchorEl={anchorEl}
  onClose={handleClose}
  anchorOrigin={{
    vertical: "bottom",
    horizontal: "center",
  }}
  transformOrigin={{
    vertical: "top",
    horizontal: "center",
  }}
  PaperProps={{
    sx: {
      p: 2.5,
      px: 3,
      maxWidth: 280,
      borderRadius: "16px",
      background: "#fff",
      boxShadow: "0 8px 24px rgba(0, 0, 0, 0.1)",
      border: "1px solid #FF9784",
      fontFamily: "'Inter', 'Poppins', sans-serif",
    },
  }}
>
  <Box sx={{ display: "flex", alignItems: "flex-start", gap: 1.5 }}>
  <Box
  sx={{
    width: 28,
    height: 28,
    backgroundColor: "#4F46E5",
    borderRadius: "50%",
    color: "#fff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    mt: "2px",
  }}
>
<AssignmentLateIcon fontSize="small" />
</Box>

    <Box>
      <Typography
        variant="subtitle2"
        sx={{
          fontWeight: 600,
          fontSize: "0.95rem",
          color: "#111827",
          mb: 0.5,
        }}
      >
        Backlogs?
      </Typography>
      <Typography
        variant="body2"
        sx={{
          color: "#4B5563",
          fontSize: "0.85rem",
          lineHeight: 1.5,
        }}
      >
        Subjects not cleared on the first attempt.
      </Typography>
    </Box>
  </Box>
</Popover>






        <TextField
          fullWidth
          name="backlogs"
          label="Enter backlogs"
          variant="outlined"
          value={formData.backlogs}
          onChange={handleChange}
          error={!!errors.backlogs}
          helperText={errors.backlogs}
          type="number"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton onClick={handleClick}>
                  <img  src={Images.info} alt="Click Me" width={26} height={28} />
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
              "& fieldset": { borderLeft: "10px solid #FFA89C" },
            },
          }}
        />
      </Box>

      {/* Navigation Buttons */}
      <Box sx={{mt: 16,
         display: "flex",
         justifyContent: "center", // Centers horizontally
         alignItems: "center", // Centers vertically
         height: "10vh", // Reduced from 100vh to 90vh to move up
         width: "100%", }}>
        <Button variant="text" sx={{ color: "#888" }} onClick={() => navigate(-1)}>
          ‚Üê Back
        </Button>
        <Button variant="contained" sx={{ px: 4, py: 1.5, borderRadius: "20px",background:"#F48438", "&:hover": {
      backgroundColor: "#d96f2e", // slightly darker shade for hover
    }, }} onClick={handleSave} disabled={!isFormValid}>
          Save
        </Button>
      </Box>
    </Box>
  );
};

export default AcademicScore;